.containerContentPageFive{
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}


.containerContentPageFiveContentPrincipal{
  width: 100vw;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.containerContentPageFiveContentPrincipalHeader{
  max-width: 90vw;
  min-width: 90vw;
  height: 10vh;
margin: 0;
  color: #FFF;
 display: flex;
 justify-content: flex-start;
 align-items: flex-start;
  font-family: Montserrat;
  .textOne{
    font-size: 2rem;
  }
  .textTwo{
    color: #FF4700;
    font-size: 7rem;
  }
}

.containerContentPageFiveContentPrincipalCentral{
  margin-top: 5vh; 
  min-height: 60vh;
  max-width: 100%;
  max-height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100%;
}

.containerContentPageFiveContentPrincipalFooter{
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
   font-family: Montserrat;
   .textOne{
     width: 50%;
     height: 100%;
     display: flex;
     justify-content: flex-end;
     align-items: center;
     font-size: 2rem;
     color: #FF4700;

   }
   .textTwo{
     width: 50%;
     height: 100%;
    color: #FFF;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-family: SummerFaith;
     font-size: 5rem;
     padding-left: 1%;
   }
}


